<template>
<div class="ecard-item-warp">
  <div class="ecard-item" :class="{'gray': cardInfo.cardStatus != 1,'ecard-normal': size == 'normal', 'ecard-small': size == 'small'}" @click="doSelect">
    <div class="ecard-header">
      <div class="ecard-header-warp">
        <div class="ecard-header-fl">
          <img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/mine/ecard/ecard_logo.png" alt="">
          <div>
            <h4>{{cardInfo.baseName || '智家E卡'}}</h4>
            <p>面值：{{cardInfo.amount}}元</p>
          </div>
        </div>
        <div class="ecard-header-fr">
          <h4 v-if="isBind || limitProd || canSelect">卡号：{{cardInfo.cardNo}}</h4>
          <h4 v-else @click.stop="getDetail(cardInfo)">消费记录</h4>
          <p>{{cardInfo.expireDate | formatDateYMD}} 到期</p>
        </div>
      </div>
      <img class="ecard-header-bg" src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/mine/ecard/ecard_header.png" alt="">
    </div>
    <div class="ecard-body">
      <div v-if="canSelect && cardInfo.cardStatus == 1" class="ecard-select" :class="{'active': cardInfo.isSelected}"><i></i></div>
      <div class="ecard-body-warp">
        <p>余额 <span>¥</span><strong>{{cardInfo.balance | formatPriceInt}}</strong><small>{{cardInfo.balance | formatPriceFloat}}</small></p>
        <a href="javascript:;" v-if="!isBind && cardInfo.cardStatus == 1" class="ecard-usebtn" @click.stop="gotoUse">适用商品</a>
      </div>
      <img class="ecard-img-footer" src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/mine/ecard/ecard_footer.png" alt="">
      <img class="ecard-img-use" v-if="cardInfo.cardStatus == 2" src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/mine/ecard/used.png" alt="">
      <img class="ecard-img-use" v-if="cardInfo.cardStatus == 3" src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/mine/ecard/ecard_guoqi.png" alt="">
    </div>
  </div>
  <div class="ecard-limit-list" @click="switchChange" v-if="limitProd">
    <p>以下商品不符合该卡使用条件</p>
    <div class="limit-imgs" v-show="switchShow">
      <div>
        <img v-for="item in limitList" :key="item.productId" :src="item.defaultImageUrl" alt="">
      </div>
    </div>
    <a href="javascript:;" class="open-btn" :class="{'active': switchShow}"></a>
  </div>
</div>
</template>

<script>
import Url from '@/http/url'

export default {
  props: {
    cardInfo: {
      type: Object,
      default: () => {}
    },
    size: {
      type: String,
      default: () => 'normal'
    },
    canSelect: {
      type: Boolean,
      default: false
    },
    isBind: {
      type: Boolean,
      default: false
    },
    limitProd: {
      type: Boolean,
      default: false
    },
    isTailOrder: {
      type: String,
      default: () => '',
    }
    // enableSelect: {
    //   type: Boolean,
    //   default: false
    // }
  },
  data() {
    return {
      limitList: [],
      switchShow: false
    }
  },
  methods: {
    getDetail(info) {
      this.$router.push(`/ecarddetail/${info.cardNo}`)
    },
    doSelect() {
      // if (!this.canSelect || this.cardInfo.cardStatus === 2) return;
      // if (this.enableSelect) {
      //   this.cardInfo.isSelected = !this.cardInfo.isSelected
      // } else {
      //   if (!this.cardInfo.isSelected) {
      //     this.$toast('目前订单中已没有可用于充抵的金额')
      //   }
      //   this.cardInfo.isSelected = false
      // }
      if (this.canSelect) {
        this.$emit('doSelect', this.cardInfo)
      }
    },
    gotoUse() {
      this.$router.push(`/ecardUseProd/${this.cardInfo.baseInfoId}?readonly=${this.limitProd ? 1 : 0}`)
    },

    switchChange() {
      let url = this.isTailOrder ? Url.getUnSupportProductsForTailOrder : Url.getUnSupportProductsForOrder
      if (!this.switchShow) {
        this.$http.post(url, {
          smartEcardId: this.cardInfo.smartEcardId,
          smartEcardBaseInfoId: this.cardInfo.baseInfoId
        }).then(res => {
          console.log(res)
          let result = res.data
          if (result.data && result.data.productList) {
            this.limitList = result.data.productList
          }
          this.switchShow = !this.switchShow
        })
      } else {
        this.switchShow = !this.switchShow
      }
    }
  }
}
</script>

