<template>
  <div class="e-card-page">
    <skeleton v-if="skeletonLoading" :nums="3"></skeleton>
    <div v-else>
      <div class="ecard-banner">
        <img class="ecard-banner-bg" src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/mine/ecard/ecard_banner.png" alt="">
        <div class="ecard-money">
          <p>礼品卡余额 <span>¥</span> <strong>{{sumAmount | formatPrice}}</strong></p>
        </div>
        <router-link to="/ecardhelp" class="ecard-help-btn">帮助说明</router-link>
      </div>
      <div class="ecard-tab">
        <a href="javascript:;" :class="{'active': curTab == 1}" @click="changeTab(1)">可用卡({{usableCount}})</a>
        <a href="javascript:;" :class="{'active': curTab == 2}" @click="changeTab(2)">不可用卡({{unusableCount}})</a>
      </div>
      <div class="ecard-list" v-if="list.length">
         <van-list
            v-model="loading"
            :finished="finished"
            finished-text="没有更多了"
            @load="onLoad"
          >
          <div v-for="item in list" :key="item.id">
            <Ecard :cardInfo="item"/>
          </div>
        </van-list>
      </div>
      <div class="list-empty" v-if="!list.length && finished">
        <img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/mine/ecard/empty.png" alt="">
        <p v-if="curTab == 1">您尚未绑定礼品卡</p>
        <p v-else>暂无不可用礼品卡</p>
      </div>
    </div>
    <div class="ecard-fixed">
      <router-link to="/ecardbind"><img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/mine/ecard/add_ecard.png" alt=""> 绑定新卡</router-link>
    </div>
  </div>
</template>

<script>
import skeleton from '@/components/skeleton'
import Ecard from './components/card'
import Url from '@/http/url'
import config from '@/config'

let cancelToken = () => {}

export default {
  name: 'Ecardlist',
  components: {
    skeleton,
    Ecard
  },
  data() {
    return {
      skeletonLoading: false,
      curTab: 1, // 当前状态 1可用 2不可用
      curPage: 1,
      pageSize: 10,
      list: [],
      loading: false,
      finished: false,
      usableCount: 0,
      unusableCount: 0,
      sumAmount: '', // 总余额
    }
  },
  activated () {
    this.resetInit()
  },
  methods: {
    resetInit() {
      this.curPage = 1;
      this.list = []
      this.loading = false
      this.finished = false
      this.fetchData()
    },
    changeTab(tab) {
      if (this.curTab === tab) return
      this.curTab = tab;
      this.resetInit()
    },
    onLoad() {
      this.fetchData()
    },
    fetchData() {
      cancelToken()
      this.loading = true
      // let params = {
      //   pageIndex: this.curPage,
      //   pageSize: this.pageSize,
      // }

      // let fetchtUrl = this.curTab === 1 ? Url.getSmartEcardUsableList : Url.getSmartEcardUnusableList
      let fetchtUrl = this.curTab === 1 ? `${Url.getSmartEcardUsableList}?pageIndex=${this.curPage}&pageSize=${this.pageSize}` : `${Url.getSmartEcardUnusableList}?pageIndex=${this.curPage}&pageSize=${this.pageSize}`

      this.$http.post(fetchtUrl, null, {
        cancelToken: new this.$http.CancelToken(c => {
          cancelToken = c;
        }),
        baseURL: config.OHOST
      }).then(({ data }) => {
        let result = data;
        if (result.success) {
          this.usableCount = result.data && result.data.usableCount
          this.unusableCount = result.data && result.data.unusableCount
          this.sumAmount = result.data && result.data.overallBalance

          if (result.data && result.data.smartEcardVOs && result.data.smartEcardVOs.length) {
            this.curPage += 1;
            this.loading = false
            this.list = [...this.list, ...result.data.smartEcardVOs]
            this.finished = result.data.smartEcardVOs.length < this.pageSize
          } else {
            this.loading = false
            this.finished = true
          }
        } else {
          this.$toast(result.errMessage)
          this.loading = false
          this.finished = true
        }
      })
    },
  }
}
</script>

